<template>
    <div class="user-promote-month-bill">
        <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="$getMonthBill"
        >
            <div class="bill-item" v-for="(item, index) in orderList" :key="index">
                <div class="bill-time row normal sm bold">
                    {{item.date}}
                </div>
                <div class="show-panel row">
                    <div class="panel-item column-center">
                        <price-slice
                            :price="item.total_money"
                            showSubscript
                            subScriptClass="sm"
                            :color="primaryColor"
                            firstClass="xxl"
                            secondClass="xxl"
                        />
                        <div class="lighter label mt5">预估收入</div>
                    </div>
                    <div class="panel-item column-center">
                        <div class="normal xxl">{{item.order_num}}</div>
                        <div class="lighter label mt5">成交笔数</div>
                    </div>
                    <div class="panel-item column-center" style="align-self: flex-end;">
                        <router-link :to="{name: 'userPromoteMonthBillDetail', query: {
                            month: item.month,
                            year: item.year
                        }}" class="row lighter">
                            查看详情<van-icon name="arrow" size="14" color="#666666" />
                        </router-link>
                    </div>
                </div>
            </div>
        </van-list>
    </div>
</template>

<script>
import {getMonthBill} from '@API/user'
export default {
    name: "userPromoteMonthBill",
    data() {
        return {
            page: 1,
            loading: true,
            finished: false,
            orderList: [],
            isEmpty: false
        }
    },
    methods: {
        $getMonthBill() {
            let {page, orderList} = this;
            this.loading = true;
            if(this.finished) return;
            getMonthBill({page_no: page}).then(res => {
                if(res.code == 1) {
                    this.loading = false;
                    let {more, list} = res.data;
                    orderList.push(...list);
                    this.orderList = orderList;
                    this.page ++;
                    if(!more) {
                        this.finished = true;
                    }
                    if(orderList.length <= 0) {
                        this.isEmpty = true;
                    }
                }
            })
        }
    },
    mounted() {
        this.$getMonthBill()
    }
}
</script>

<style lang="scss" scoped>
.user-promote-month-bill {
    .bill-item {
        .bill-time {
            padding: 10px;
            line-height: 17px;
        }
        .show-panel {
            background-color: white;
            padding: 18px 0 13px;
            .panel-item {
                flex: 1;
                line-height: 17px;
            }
        }
    }
}
</style>